#home {
    font-size: 2rem;
    min-width: 100%;
    padding: 220px 0;
    background-color: #FFF;
    background-image: url(/src/Image/computer-on-desk.webp);
    background-size: cover;
    background-repeat: no-repeat;
    /*-webkit-filter: grayscale(100%);
    filter: grayscale(100%);*/
    height: 500px;
}
#home:before {
    content: '';
    position: relative;
    top: 0;
    min-width: 100%;
    height: 100%;
    background-color: rgba(120,192,224, 0.65);
}
#home #home-inner {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 5%;
    gap: 3%;
    background-color: rgba(0,48,143, 0.65);
    color: #FFF;
}
#home h1, #home h2 {
    width: 100%;
}
