#home-inner button {
    background-color: transparent;
    border: 4px solid #FFF;
    font-size: 18px;
    padding: 15px 20px;
    font-weight: 700;
    font-family:'Rubik', sans-serif;
    transition: ease-out 0.3s;
    cursor: pointer;
}
#home-inner button a { 
    color: #FFF;
    text-decoration: none;
}
#home-inner button:hover { 
    text-decoration: underline;
    text-decoration-color: #FFF;
}
/*#home-inner button:hover, #home-inner button:focus {
    background-color: #0E0E52;
    color: #FFF; 
    border: 4px solid #0E0E52;
}*/
#home-inner h2 u {
    text-decoration: underline;
    text-decoration-color: rgba(0,48,143);
}
#home-inner h2 {
    font-family: Helvetica;
    font-weight: 300;
}
#home-inner h1 {
    font-family:'Rubik', sans-serif;
    max-width: 600px;
}

@media (max-width: 888px) {
#home #home-inner {
    flex-direction: column;
}
#home-inner h1 {
    margin-bottom: 0;
}
#home-inner h1 { 
        max-width: unset;
}
#home {
    height: 100%;
    padding: 110px 0;
}
#home-inner h2 {
    font-size: 25px;
}
}

@media (max-width: 500px) {
    #home #home-inner h1 {
        font-size: 3.5rem;
    }
}