#contact p {
    font-family: Helvetica;
    font-weight: 700;
    font-size: 16px;
}
#contact {
    padding-top: 10vh;
    background-color: #FFF;
    
}
#contact input, #contact textarea {
    border: 3px solid rgba(0,48,143);
    border-radius: 10px;
    min-height: 25px;
    min-width: 200px;
    width: 40%;
    height: 35px;
}
#contact div {
    display: flex;
    align-items: center;
    gap: 2.5%;
    padding-bottom: 33px;
    padding-left: 5%;
    padding-right: 5%;
}
#contact #TwoColumnText {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 0px 5%;
    gap: 3%;
}
#contact #TwoColumnText .rightSide h2 {
    font-family: Helvetica;
    font-weight: 300;
}
#phone-input, #email-input {
    width: 20% !important;
}
#contact-form {
    display: flex;
    flex-direction: column;
    padding-left: 5%;
    padding-right: 5%;
    padding-bottom: 110px;
}
#contact-form input, #contact-form textarea {
    width: 100%;
}
#contact-form textarea {
    min-height: 100px;
    resize: none;
}
/*#contact-form #section1 input, #contact-form #section3 textarea {
    min-width: 924px;
}*/
#contact-form #section1 label, #contact-form #section2 label {
    min-width: 66px;
}
#contact-form button[type='submit'] {
    margin-left: calc(7.5% + 66px);
    margin-right: 5%;
    background-color: rgba(0,48,143);
    color: #FFF;
    border-radius: 10px;
    padding: 10px;
    border: none;
    border: 3px solid #FFF;
}
#contact-form button[type='submit']:hover {
    background-color: #FFF;
    color: rgba(0,48,143);
    border: 3px solid rgba(0,48,143);
    cursor: pointer;
}
#contact-form button:active {
    transform: translateY(4px);
}
#contact-form button[type='submit'] { 
    font-weight: 700;
    font-family:'Rubik', sans-serif;
    font-size: 18px;
}
@media (min-width: 888px) {
    .s2-mobile {
        display: none !important;
    }
}
@media (max-width: 888px) {
    #contact #TwoColumnText { flex-direction: column; }
    #contact #TwoColumnText {  max-width: unset; }
    #contact #TwoColumnText  { align-items: baseline; }
    #contact #TwoColumnText h1 {
        margin-bottom: 0;
    }
    .s2-desktop {
        display: none !important;
    }

}