#services {
    padding: 10vh 5%;
    background-color: #FFF;
}
#services h3 {
    font-size: 24px;
    text-align: center;
    padding-top: 50px;
}
#services #our-process p {
    font-size: 18px;
}
#services button {
    background-color: transparent;
    border: 4px solid #000;
    font-size: 18px;
    padding: 15px 20px;
    font-weight: 700;
    font-family:'Rubik', sans-serif;
    transition: ease-out 0.3s;
    cursor: pointer;
    display: block;
    margin-top: 0.83em;
}
#services button a {
    color: #000;
    text-decoration: none;
}
#services button:hover, #services button:focus {
    text-decoration: underline;
    /*background-color: #0E0E52;
    color: #FFF; 
    border: 4px solid #0E0E52;*/
}
#services #single-column-text {
    border-width: 7px;
    padding: 0px 0px;
    margin: 0px 5% 40px 0px;
}
#our-process {
    border-top: 7px dashed #0E0E52;
    border-bottom: 7px dashed #0E0E52;
}
#our-process-inner {
    display: flex;
    position: relative;
    flex-direction: row;
    align-items: center;
    column-gap: 5%;
}
#our-process-inner p {
    width: 33%;
    padding-top: 50px;
    text-align: center;
}
