    position: relative;
    font-family: "Font Awesome 5 Free";
    display: flex;
    justify-content: center;
    margin-bottom: 25px;
}
#our-process-inner p:nth-child(2)::before {
    content: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" height="45px" width="45px" viewBox="0 0 512 512"><!--! Font Awesome Free 6.4.2 by @fontawesome - https://fontawesome.com License - https://fontawesome.com/license (Commercial License) Copyright 2023 Fonticons, Inc. --><style>svg{fill:%230e0e52}</style><path d="M164.9 24.6c-7.7-18.6-28-28.5-47.4-23.2l-88 24C12.1 30.2 0 46 0 64C0 311.4 200.6 512 448 512c18 0 33.8-12.1 38.6-29.5l24-88c5.3-19.4-4.6-39.7-23.2-47.4l-96-40c-16.3-6.8-35.2-2.1-46.3 11.6L304.7 368C234.3 334.7 177.3 277.7 144 207.3L193.3 167c13.7-11.2 18.4-30 11.6-46.3l-40-96z"/></svg>');
    position: relative;
    font-family: "Font Awesome 5 Free";
    display: flex;
    justify-content: center;
    margin-bottom: 25px;
}
#our-process-inner p:nth-child(3)::before {
    content: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" height="45px" width="45px" viewBox="0 0 512 512"><!--! Font Awesome Free 6.4.2 by @fontawesome - https://fontawesome.com License - https://fontawesome.com/license (Commercial License) Copyright 2023 Fonticons, Inc. --><style>svg{fill:%230e0e52}</style><path d="M256 48a208 208 0 1 1 0 416 208 208 0 1 1 0-416zm0 464A256 256 0 1 0 256 0a256 256 0 1 0 0 512zM369 209c9.4-9.4 9.4-24.6 0-33.9s-24.6-9.4-33.9 0l-111 111-47-47c-9.4-9.4-24.6-9.4-33.9 0s-9.4 24.6 0 33.9l64 64c9.4 9.4 24.6 9.4 33.9 0L369 209z"/></svg>');
    position: relative;
    font-family: "Font Awesome 5 Free";
    display: flex;
    justify-content: center;
    transform: translateY(-25px);
}
#our-process-inner p:nth-child(1), #our-process-inner p:nth-child(2){
    min-height: 250px;
}
#our-process-inner p:nth-child(3) {
    min-height: 200px;
}
.rightSide {
    padding-bottom: 40px;
    margin-bottom: 40px;
}
#img-side-by-side-container {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 50px;
}
#web-development-container #TwoColumnText {
    background-color: #FFF;
    padding-top: 40px;
    margin: 0px;
}
@media (max-width: 1030px) {
#img-side-by-side-container {
    flex-direction: column;
}
#profilio-desktop-img1, #profilio-mobile-img1 {
    height: 100% !important;
    width: 100%;
    object-fit: cover !important;
}
#profilio-desktop-img1 {
    object-fit: contain;
}
}
@media (max-width: 1024px) {
    #our-process-inner {
        flex-wrap: wrap;
        justify-content: center;
    }
    #services {
        padding: 10vh 5%;
    }
}
@media (max-width: 660px) {
    #our-process-inner {
        flex-direction: column;
    }
    #our-process-inner p {
        width: 100%;
        padding-top: 0;
    }
    #services h3 {
        padding-top: 20px;
    }
    #our-process-inner p:nth-child(1), #our-process-inner p:nth-child(2) {
        min-height: unset;
    }
    #our-process-inner p:nth-child(3)::before {
        transform: unset;
    }
    #our-process-inner p:nth-child(3) {
        min-height: unset;
        padding-bottom: 20px;
    }
    #services #single-column-text {
        margin: 20px 5%;
        padding: 20px 0px;
    }
    #our-process-inner p:nth-child(3)::before {
        margin-bottom: 25px;
    }
    #TwoColumnText .leftSide h1 {
        font-size: 12vw;
    }
    #services {
        padding: 5vh 5%;
    }
}