
  @import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&family=Rubik:wght@400;600&display=swap');
  
  .headerContainer ul {
    display: flex;
    gap:  4.5%;
    justify-content: center;
    padding-right: 0; padding-left: 0;

  }
  .headerContainer {
    max-width: 70%;
    margin: 0 auto;
  }
  .headerContainer ul oi, .headerContainer a {
    font-family: 'Rubik', sans-serif;
    font-weight: 700;
    font-size: 2.5vw;
    color: #FFF;
    text-decoration: none;
    white-space: nowrap;
  }
  .headerContainer ul oi:hover, .headerContainer a:hover {
    margin-top: -3px;
    text-decoration: underline;
    cursor: pointer;
  }
  .headerContainer ul .site-identity {
    font-size: 4.5vw;
    padding-left: 3%;
    padding-right: 3%;
    -webkit-user-select: none; /* Safari */
    user-select: none; /* Standard syntax */
  }
.mobileHeaderContainer {
  min-width: 100%;
  height: 50px;
}
  /*Menu Icon*/
  .container {
    display: inline-block;
    cursor: pointer;
  }
  .line1, .line2, .line3 {
    width: 35px;
    height: 5px;
    background-color: #FFF;
    margin: 6px 0;
    transition: 0.4s;
    border-radius: 5px;
  }
  .change .line1 {
    transform: translate(0, 11px) rotate(-45deg);
  }
  .change .line2 {opacity: 0;}
  .change .line3 {
    transform: translate(0, -11px) rotate(45deg);
  } 
  #footerNavMenu {
    background-color: #FFF;
    min-width: 100%;
    transition: 3s ease;
  } 
  #footerNavMenu ul {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 30px;
    padding: 25px 0px;
  }
  #footerNavMenu ul a {
    color: #00308F;
    text-decoration: none;
    font-weight: 700;
    font-size: 1.5rem;
  }
  /*Menu Icon*/
@media (min-width: 889px) { .mobileHeader, .mobileHeaderContainer { display: none; }}
@media (max-width: 888px) {
.desktopHeader .headerInner ul a:nth-child(1), .desktopHeader .headerInner ul a:nth-child(2), .desktopHeader .headerInner ul a:nth-child(4) {
  display: none;
}
  .mobileHeader {
    right: 5%;
    position: absolute;
    top: 23px;
    display: flex;
    min-width: 35px;
    min-height: 39px;
    justify-content: center;
    align-items: center;
    cursor: pointer;
   }
   .site-identity {
    position: absolute;
    font-size: 45px !important;
    left: 5%;
    padding-left: 0;
   }
}
