.footerContainer ul {
    display: flex;
    flex-direction: row;
    padding-top: 5%;
    padding-bottom: 5%;
    /*padding: 5%;*/
    gap: 15px;
    align-items: center;
}
.footerContainer {
    background-color: rgba(0,48,143);
    display: flex;
    align-items: center;
}
.footerContainer ul oi, .footerContainer ul a {
    font-family: 'Rubik', sans-serif;
    font-weight: 700;
    font-size: 20px;
    color: #FFF;
    text-decoration: none;
}
.footerContainer .site-identity {
    font-size: 5.5vw;
    color: #FFF;
    font-family: 'Rubik', sans-serif;
    font-weight: 700;
    user-select: none;
    margin: 0;
    padding-left: 5%;
}
.footerContainer #get-in-touch {
    background-color: transparent;
    border: 4px solid #FFF;
    color: #FFF;
    font-size: 20px;
    padding: 15px 20px;
    font-weight: 700;
    font-family: 'Rubik', sans-serif;
    transition: ease-out 0.3s;
    cursor: pointer;
    margin-left: 15px;
    z-index: 2;
    text-align: center;
}
.footerContainer #get-in-touch:hover, .footerContainer #get-in-touch oi:hover {
    background-color: #FFF !important;
    color: rgba(0,48,143) !important;
}
.footerContainer #get-in-touch oi {
    font-size: 20px;
}
.company-email {
    color: #FFF;
    font-family: 'Rubik', sans-serif;
    display: flex;
    gap: 15px;
}
.company-email:before {
    content: url('./Image/envelope.svg');
    position: relative;
    max-width: 24px;
    max-height: 24px;
    filter: brightness(0) invert(1);
}
.footerContainer .social-info {
    padding-left: 15%;
    padding-right: 5%;
}
@media (min-width: 1500px) {
    .footerContainer ul {
        gap: 4.5%;
    }
    #get-in-touch {
        min-width: 125px;
    }
}
@media (max-width: 960px) {
    .footerContainer ul, .footerContainer {
        flex-direction: column;
    }
    .footerContainer .site-identity, .footerContainer ul, .footerContainer .social-info {
        padding-left: 0;
    }
    .footerContainer .social-info {
        padding-right: 0;
    }
    .footerContainer {
        padding: 3% 0;
    }
    .footerContainer ul {
        padding-bottom: 3%;
        margin-bottom: 0;
        padding-top: 3%;
    }
    .footerContainer #get-in-touch {
        margin-left: 0;
    }
}