/*Project 1*/
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;400;500;700&family=Orbitron:wght@400;500;600&display=swap');
/*Banner*/
.topBanner {
	background-color: #0A2647;
  padding-top: 1%;
  padding-left: 3%;
  padding-bottom: 1%;
	display: flex;
}
.topBanner p {
	margin-bottom: 0;
  color: #FFF;
  font-family: 'Montserrat', sans-serif;
  font-size: 24px;
  font-weight: 600;
}
.topBanner a {
	color: #FFF !important;
}
.topBannerSocialIcons {
	position: absolute;
	right: 0%;
}
.bannerSocial {
	font-size: 25px;
}
.icons-gap {
	gap: 25px;
}
/*Banner*/
/*Header*/
.ast-primary-header-bar {
	background-color: #205295;
	border: 0;
}
#ast-hf-menu-1 li a {
	color: #FFF;
}
/*Header*/

/*Section 1*/
.section1 {
	padding-bottom: 125px;
}
.s1Inner {
	position: relative;
	min-width: 100%;
}
.s1SectionOverVideo {
	position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
	background-color: rgba(44, 116, 179, 0.85);
  border: 4px solid #FFF;
  border-radius: 4px;
}
.s1SectionOverVideo p {
	padding-left: 20px;
	padding-right: 20px;
	font-family: 'Montserrat', sans-serif;
}
.s1SectionOverVideo h1 {
	color: #FFF;
	font-weight: 700;
	margin-bottom: 5px;
	text-align: center;
	font-family: 'Orbitron', sans-serif;

}
.s1SectionOverVideo p {
	color: #FFF;
	font-weight: 700;
	text-align: center;
}
.s1SectionOverVideo hr {
	background-color: #FFF;
  height: 4px;
}
.s1HeroImage {
    background-image: url('../Image/hero-img.jpg');
   height: 550px;
   width: auto;
   background-size: cover;
}
/*Section 1*/

/*Section 2*/
.s2Inner {
	display: flex;
  justify-content: center;
  padding-bottom: 125px;
	gap: 50px;
	padding-left: 20px;
	padding-right: 20px;
}
.s2Img1 {
  background-image: url("../Image/service-img1.jpg");
  height: 500px;
  background-size: cover;
  max-width: 400px;
  background-repeat: no-repeat;
  border-radius: 4px;
	background-position: center;
	margin-left: auto;
	margin-right: auto;
}
.s2Left, .s2Right {
  width: 400px;
}
.s3Right h2 {
	font-family: 'Orbitron', sans-serif;
}
.s3Right p {
	font-family: 'Montserrat', sans-serif;
}
.s2Left h2, .s2Right h2 {
	text-align: center;
	padding: 25px 0px;
	margin-bottom: 0px;
	font-family: 'Orbitron', sans-serif;
}
.s2Left p, .s2Right p {
  text-align: center;
	font-family: 'Montserrat', sans-serif;
}
.s2Left hr, .s2Right hr {
	background-color: #205295;
  height: 4px;
  border-radius: 4px;
  max-width: 75%;
  margin-left: auto;
  margin-right: auto;
}
.s2Left button, .s2Right button {
  margin-left: auto;
  margin-right: auto;
  display: block;
  background-color: #205295;
  border-radius: 4px;
  border: 4px solid #205295;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  width: 162px;
  height: 54px;
  color: #FFF;
}
.s2Left button:hover, .s2Right button:hover {
	border: 4px solid #205295;
	background-color: #FFF;
	color: #205295;
}
.s2Left:hover, .s2Right:hover {
	background-color: rgba(0, 0, 0, 0.05);
}
.s2Img2 {
  background-image: url("../Image/service-img2.jpg");
  height: 500px;
  background-size: cover;
  max-width: 400px;
  background-repeat: no-repeat;
  border-radius: 4px;
	background-position: center;
}
/*Section 2*/

/*Section 3*/
.section3 {
	padding-bottom: 125px;
	padding-top: 125px;
}
.s3Inner {
  display: flex;
  flex-direction: row;
  justify-content: center;
	gap: 50px;
	align-items: center;
	padding-left: 20px;
	padding-right: 20px;
}
.s3Left img {
  border-radius: 4px;
  max-width: 100%;
}
.s3Left, .s3Right {
	width: 50%;
}
.s3Right hr {
    background-color: #205295;
    height: 4px;
    border-radius: 4px;
    max-width: 45%;
}
/*Section 3*/

@media screen and (max-width: 932px) {
	.s2Inner {
		padding-bottom: 0px;
	}
}
@media screen and (max-width: 750px) {
	.s3Inner {
		flex-direction: column;
	}
	.s3Left, .s3Right {
		width: 90%;
	}
}
@media screen and (max-width: 735px) {
	.social-icons {
		display: flex;
    flex-direction: row;
    padding-top: 85px;
    justify-content: center;
    width: 100%;
	}
	.topBannerSocialIcons {
		display: none;
	}
	.topBanner {
    padding-top: 2%;
    padding-bottom: 2%;
    justify-content: center;
	}
	.topBanner p {
		text-align: center;
		padding-left: auto;
		padding-right: auto;
	}
}
@media screen and (max-width: 650px) {
	.s2Inner {
		flex-direction: column;
		align-items: center;
	}
	.section1 {
		padding-bottom: 65px;
	}
	.section3 {
		padding-top: 65px;
		padding-bottom: 65px;
	}
}
@media screen and (max-width: 570px) {
	.s1SectionOverVideo {
		width: 75%;
	}
}
#project1 {
    padding: 25px;
    border: 3px solid #000;
    margin: 25px;
}
#profolio-example-text {
    margin-left: 25px;
    font-size: 24px;
}
/* Project 1 */
